<template>
  <div>
    <ul class="notice">
      <li>
        <svg class="icon icon1" aria-hidden="true">
          <use xlink:href="#icon-xinwengonggao"></use>
        </svg>
        <span class="notice_text">新闻中心</span>
      </li>
      <li class="line">
        <svg class="icon icon2" aria-hidden="true">
          <use xlink:href="#icon-xiantiao"></use>
        </svg>
      </li>
    </ul>
    <van-swipe :autoplay="3000">
      <van-swipe-item v-for="(item, index) in swiperSlides" :key="index">
        <van-image lazy-load :src="item.imageurl" />
      </van-swipe-item>
    </van-swipe>
    <van-pull-refresh v-model="isLoading" @refresh="getNewsList">
      <van-list
        class="list"
        v-model="loading"
        @load="getNewsList"
        :finished="finished"
      >
        <van-cell
          is-link
          class="van-multi-ellipsis--l3"
          v-for="(item, index) in newslist"
          :key="index"
          :label="item.time"
          @click="handleToDetail(item.id)"
        >
          <template #title>
            <span class="custom-title">{{ item.title }}</span>
            <svg v-if="index < 2" class="icon icon1" aria-hidden="true">
              <use xlink:href="#icon-NEW"></use>
            </svg>
          </template>
        </van-cell>
      </van-list>
      <van-pagination
        v-model="currentPage"
        :page-count="pageNum"
        @change="getNewsList"
        mode="simple"
      />
    </van-pull-refresh>
  </div>
</template>

<script>
import '../../../public/assets/iconfont'
export default {
  name: 'News',
  data() {
    return {
      newslist: [],
      swiperSlides: [],
      loading: false,
      isLoading: false,
      finished: false,
      currentPage: 1,
      pageNum: 1
    }
  },
  methods: {
    async getNewsList() {
      let res = await this.$http.get('/api/home/news')
      if (res.status !== 200) return false
      if (res.data.code != 200) return false
      this.newslist = res.data.data.list
      this.pageNum = res.data.data.pageNum
      this.loading = false
      this.isLoading = false
      this.finished = true
    },
    async getSwiperList() {
      let res = await this.$http.get('/api/home/getImage')
      if (res.status !== 200) return false
      if (res.data.code != 200) return
      this.swiperSlides = res.data.data
    },
    handleToDetail(id) {
      this.$router.push('/news/detail/' + id)
    }
  },
  mounted() {
    this.getNewsList()
    this.getSwiperList()
  }
}
</script>

<style scoped>
.line {
  font-size: 0;
}
.icon1 {
  padding-top: 0.5rem;
  width: 1.25rem;
  height: 1.25rem;
}
.icon2 {
  width: 7.5rem;
  height: 0.5rem;
  fill: #f9ca4f;
}
.notice {
  width: 7.5rem;
  margin-left: 0.5rem;
  font-size: 1.25rem;
  margin-bottom: 1.25rem;
}
.notice_text {
  margin-left: 0.375rem;
}
.list {
  min-height: 15rem;
}
</style>
